import React, { useState, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { Container, Row, Col } from 'reactstrap';
import Gallery from 'react-photo-gallery';
import Carousel, { Modal, ModalGateway } from 'react-images';
import bolImage1 from '../../assets/images/projets/Web/BriefOnline/1_Bol_OngoingProject.jpg';
import bolImage2 from '../../assets/images/projets/Web/BriefOnline/2_Bol_AddSupport.jpg';
import bolImage3 from '../../assets/images/projets/Web/BriefOnline/3_Bol_Support.jpg';
import bolImage4 from '../../assets/images/projets/Web/BriefOnline/4_Bol_Workflow.jpg';
import '../../scss/ProjetDetails.scss';

const photos = [
  {
    src: bolImage1,
    width: 4,
    height: 2
  },
  {
    src: bolImage2,
    width: 4,
    height: 2
  },
  {
    src: bolImage3,
    width: 4,
    height: 2
  },
  {
    src: bolImage4,
    width: 4,
    height: 2
  }
];

export default function BriefOnline() {
  const stack = [
    'AngularJS',
    'Java',
    'Javascript',
    'MySQL',
    'Html',
    'Css',
    'Sass',
    'Gulp',
    'Material Design'
  ];

  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <Container className='details-projet-container'>
      <Row>
        <Col xs='auto'>
          <h1 className='title-details-projet'>Brief Online</h1>
        </Col>
      </Row>
      <Row className='text-details-projet justify-content-center'>
        <Col xs='12' lg='9'>
          <p>
            La web application « Brief Online » permet aux clients d’Altavia
            d’envoyer un brief de leurs besoins et d’avoir un suivi de leurs
            projets. Le client peut gérer ses campagnes et y inclure des
            supports (brochure, catalogue, affiche, roll-up, PLV...) qu&apos;il
            pourra paramétrer à souhait.
          </p>
          <p>Enjeux client :</p>
          <ul>
            <li>
              Centraliser les besoins des clients d’Altavia sur une même
              plateforme
            </li>
            <li>Assurer un suivi pertinant et adaptatif pour chaque client</li>
            <li>
              Permettre un échange simplifié entre les clients et les
              différentes business units (BU) d’Altavia.
            </li>
          </ul>
        </Col>
        <Col xs='12' lg='9'>
          <p>Année: 2019</p>
        </Col>
        <Col xs='12' lg='9'>
          <p>Catégorie: Développement web</p>
        </Col>
        <Col xs='12' lg='9'>
          <a href='https://www.altavia-group.com/fr/' className='link-unstyled'>
            www.altavia-group.com
          </a>
        </Col>
      </Row>
      <Row className='justify-content-center align-items-center p-1'>
        {stack.length &&
          stack.map(e => (
            <Col xs='auto' className='p-2' key={e}>
              <p className='label-stack'>{e}</p>
            </Col>
          ))}
      </Row>
      <Gallery photos={photos} onClick={openLightbox} />
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={photos.map(x => ({
                ...x,
                srcset: x.srcSet,
                caption: x.title
              }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>
      <Row className='justify-content-center mt-5'>
        <Col xs='auto'>
          <Link
            to='/mooviz'
            className='link-switch-project hvr-sweep-to-right'
            smooth='true'
          >
            Projet
            <br />
            suivant
          </Link>
        </Col>
      </Row>
    </Container>
  );
}
