import React, { useState, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { Container, Row, Col } from 'reactstrap';
import Gallery from 'react-photo-gallery';
import Carousel, { Modal, ModalGateway } from 'react-images';
import viParisLaPlazaImage1 from '../../assets/images/projets/3D/BullitStudio/1_Monuments.jpg';
import viParisLaPlazaImage2 from '../../assets/images/projets/3D/BullitStudio/2_NotreDame.jpg';
import viParisLaPlazaImage3 from '../../assets/images/projets/3D/BullitStudio/3_Invalides.jpg';
import viParisLaPlazaImage4 from '../../assets/images/projets/3D/BullitStudio/4_SacreCoeur.jpg';
import viParisLaPlazaImage5 from '../../assets/images/projets/3D/BullitStudio/5_ArcDeTriomphe.jpg';
import viParisLaPlazaImage6 from '../../assets/images/projets/3D/BullitStudio/6_MonumentsWireFrame.jpg';
import '../../scss/ProjetDetails.scss';

const photos = [
  {
    src: viParisLaPlazaImage1,
    width: 8,
    height: 4
  },
  {
    src: viParisLaPlazaImage2,
    width: 11,
    height: 4
  },
  {
    src: viParisLaPlazaImage3,
    width: 11,
    height: 4
  },
  {
    src: viParisLaPlazaImage4,
    width: 11,
    height: 4
  },
  {
    src: viParisLaPlazaImage5,
    width: 12,
    height: 4
  },
  {
    src: viParisLaPlazaImage6,
    width: 10,
    height: 2
  }
];

export default function ViParisLaPlaza() {
  const stack = ['Maya', 'Arnold', 'Substance painter', 'Photoshop'];

  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <Container className='details-projet-container'>
      <Row>
        <Col xs='auto'>
          <h1 className='title-details-projet'>Viparis la Plaza</h1>
        </Col>
      </Row>
      <Row className='text-details-projet justify-content-center'>
        <Col xs='12' lg='9'>
          <p>
            Au sein de Bullit Studio, mes missions consistées en la
            modélisation, le texturing, le lighting et le rendu 3D des monuments
            de Paris utilisés dans projet pour ViParis La Plaza.
            <br />
            L&apos;écran de la Plaza situé porte de Versailles, de par sa forme
            unique, permet une orientation à 360°. Utilisé pour indiquer quels
            événements se déroulent dans quels pavillons, l&apos;écran peut
            étendre son pouvoir d&apos;indication au niveau parisien, national
            et même mondial.
            <br />
            Dans un écrin autant graphique que pédagogique, l&apos;écran de la
            Plaza permettra d&apos;affirmer l&apos;identité visuelle de Viparis,
            tout en offrant une fenêtre culturelle aux visiteurs.
            <br />
            Dans ce projet, l&apos;écran de la Plaza indique la direction où se
            trouvent les principaux monuments parisiens, accompagnés de la
            distance à vol d&apos;oiseau, en kilomètres et en miles, qui sépare
            la Plaza et le monument.
          </p>
        </Col>
        <Col xs='12' lg='9'>
          <p>Année: 2019</p>
        </Col>
        <Col xs='12' lg='9'>
          <p>Catégorie: Infographie 3D</p>
        </Col>
        <Col xs='12' lg='9'>
          <a href='http://bullit-studio.fr' className='link-unstyled'>
            www.bullit-studio.fr
          </a>
        </Col>
      </Row>
      <Row className='justify-content-center align-items-center p-1'>
        {stack.length &&
          stack.map(e => (
            <Col xs='auto' className='p-2' key={e}>
              <p className='label-stack'>{e}</p>
            </Col>
          ))}
      </Row>
      <Gallery photos={photos} onClick={openLightbox} />
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={photos.map(x => ({
                ...x,
                srcset: x.srcSet,
                caption: x.title
              }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>
      <Row className='justify-content-center mt-5'>
        <Col xs='auto'>
          <Link
            to='/hello-world'
            className='link-switch-project hvr-sweep-to-left'
            smooth='true'
          >
            Projet
            <br />
            précédent
          </Link>
          <Link
            to='/la-defense-arena'
            className='link-switch-project hvr-sweep-to-right'
            smooth='true'
          >
            Projet
            <br />
            suivant
          </Link>
        </Col>
      </Row>
    </Container>
  );
}
