import React, { useState, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { Container, Row, Col } from 'reactstrap';
import Gallery from 'react-photo-gallery';
import Carousel, { Modal, ModalGateway } from 'react-images';
import HeisenbergImage from '../../assets/images/projets/2D/Heinsenberg.jpg';
import '../../scss/ProjetDetails.scss';

const photos = [
  {
    src: HeisenbergImage,
    width: 7,
    height: 4
  }
];

export default function LaDefenseArena() {
  const stack = ['Illustrator', 'Tablette graphique', 'Papier & crayon'];

  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <Container className='details-projet-container'>
      <Row>
        <Col xs='auto'>
          <h1 className='title-details-projet'>Heisenberg</h1>
        </Col>
      </Row>
      <Row className='text-details-projet justify-content-center'>
        <Col xs='12' lg='9'>
          <p>
            Lorem ipsum dolor sit, amet consectetur adipisicing elit. Architecto
            molestiae et, ipsum reprehenderit similique animi, corporis nemo
            officiis quasi dicta ut repudiandae accusamus? Deleniti voluptas
            iste autem temporibus a reiciendis.
          </p>
        </Col>
        <Col xs='12' lg='9'>
          <p>Année: 2018</p>
        </Col>
        <Col xs='12' lg='9'>
          <p>Catégorie: Infographie 2D</p>
        </Col>
      </Row>
      <Row className='justify-content-center align-items-center p-1'>
        {stack.length &&
          stack.map(e => (
            <Col xs='auto' className='p-2' key={e}>
              <p className='label-stack'>{e}</p>
            </Col>
          ))}
      </Row>
      <Row className='justify-content-center'>
        <Col xs='11' lg='9'>
          <Gallery photos={photos} onClick={openLightbox} />
          <ModalGateway>
            {viewerIsOpen ? (
              <Modal onClose={closeLightbox}>
                <Carousel
                  currentIndex={currentImage}
                  views={photos.map(x => ({
                    ...x,
                    srcset: x.srcSet,
                    caption: x.title
                  }))}
                />
              </Modal>
            ) : null}
          </ModalGateway>
        </Col>
      </Row>
      <Row className='justify-content-center mt-5'>
        <Col xs='auto'>
          <Link
            to='/hells-kitchen'
            className='link-switch-project hvr-sweep-to-left'
            smooth='true'
          >
            Projet
            <br />
            précédent
          </Link>
          <Link
            to='/flyer'
            className='link-switch-project hvr-sweep-to-right'
            smooth='true'
          >
            Projet
            <br />
            suivant
          </Link>
        </Col>
      </Row>
    </Container>
  );
}
